<template>
  <section class="todoapp">
    <header class="header">
      <h1>todos</h1>
      <input
        class="new-todo"
        placeholder="What needs to be done?"
        autofocus
        @keyup.enter="addList"
      />
    </header>
    <!-- This section should be hidden by default and shown when there are todos -->
    <!-- <router-view :showList="showList" :test="test"></router-view> -->
    <section class="main">
      <!-- <div>{{ $route.query.test }}</div> -->
      <input
        id="toggle-all"
        class="toggle-all"
        type="checkbox"
        @click="toggleAll"
      />
      <label for="toggle-all">Mark all as complete</label>
      <ul class="todo-list">
        <!-- These are here just to show the structure of the list items -->
        <!-- List items should get the class `editing` when editing and `completed` when marked as completed -->
        <!-- li的completed是在字体上加上下划线 -->
        <li
          :class="item.clickStatus ? 'completed' : ''"
          v-for="(item, index) in viewList"
          :key="index"
        >
          <div class="view">
            <!-- 默认是checkbox选中 -->
            <input
              class="toggle"
              type="checkbox"
              :checked="viewList[index].clickStatus"
              @click="clickStatus($event, index)"
            />
            <label>{{ item.value }}</label>
            <button class="destroy" @click="deleteList(index)"></button>
          </div>
          <input class="edit" value="Create a TodoMVC template" />
        </li>
      </ul>
    </section>
    <!-- This footer should be hidden by default and shown when there are todos -->
    <footer class="footer" v-if="showList.length">
      <!-- This should be `0 items left` by default -->
      <span class="todo-count"
        ><strong>{{ list_length }}</strong> item left</span
      >
      <!-- Remove this if you don't implement routing -->
      <ul class="filters">
        <li>
          <a class="selected" href="javascript:;" @click="toShowAll">All</a>
        </li>
        <li>
          <a href="javascript:;" @click="showActive">Active</a>
        </li>
        <li>
          <a href="javascript:;" @click="showCompleted">Completed</a>
        </li>
      </ul>
      <!-- Hidden if no completed items are left ↓ -->
      <button class="clear-completed" @click="clearCompleted">
        Clear completed
      </button>
    </footer>
  </section>
</template>
<script>

import axios from 'axios'
export default {
  name: "Home",
  data() {
    return {
      // 用于渲染list，存放list用户输入的数据
      showList: [],
      viewList: [],
      list_length: 0,
      test: {
        name: "tom",
        age: 18,
      },
      id:0,
    };
  },
  async created(){
    const res = await axios.get('/all')
    if(res){
      console.log(res);
      this.showList = res.data;
    }else{
      console.log("失败");
    }
  },
  methods: {
    // 添加list
    addList($event){
      axios({
        method:'post',
        url:'/content',
        params:{
          value:$event.target.value,
          clickStatus:false,
          id:this.id
        }

      })
      this.id++;
    },
    // 修改数据
    clickStatus($event, index) {
      // 修改数据
      axios({
        method:'post',
        url:"/patch",
        params:{
          itemId:this.showList[index].id,
          status:$event.target.checked
        }
      })
      // this.showList[index].clickStatus = $event.target.checked;
      // console.log(this.showList);
    },
    // 显示全部list
    async toShowAll(){
      // axios.get({
      //   url:'/all'
      // }).then(res=>{
      //   console.log(res);
      //   this.showList.push(...res)
      // })
      const res = await axios.get('/all')
      if(res){
        console.log(res);
        // this.showList.push(...res)
        this.showList = res.data;
      }else{
        console.log("没有接收到数据");
      }
    },
    async toShowActive() {
      // this.$router.push({ name: "Active" });
      // this.viewList = this.showList.filter((e) => e.clickStatus == false);
      const res = await axios({
        method:"get",
        url:'/active'
      })
      if(res){
        this.showList = res.data
      }
    },
    async toShowCompleted() {
      // this.$router.push({ name: "Completed" });
      // this.viewList = this.showList.filter((e) => e.clickStatus == true);
      const res = await axios({
        method:"get",
        url:'/completed'
      })
      if(res){
        this.showList = res.data
      }
    },




    addToDo($event) {
      console.log(this.showList);
      this.showList.push({ value: $event.target.value, clickStatus: false ,id:this.id});
      this.list_length++;
      
      $event.target.value = "";
      this.viewList = this.showList;
    },
    
    showAll() {
      // this.$router.push({ name: "All" });
      this.viewList = this.showList;
    },
    showActive() {
      // this.$router.push({ name: "Active" });
      this.viewList = this.showList.filter((e) => e.clickStatus == false);
    },
    showCompleted() {
      // this.$router.push({ name: "Completed" });
      this.viewList = this.showList.filter((e) => e.clickStatus == true);
    },
    // 删除标签
    deleteList(index) {
      this.showList.splice(index, 1);
      this.list_length--;
    },
    // 删除已经完成的list
    clearCompleted() {
      this.showList = this.showList.filter((e) => !e.clickStatus == true);
      this.viewList = this.showList;
      this.list_length = this.viewList.length;
    },
    // 全选
    toggleAll() {
      if (this.showList.every((e) => e.clickStatus == true)) {
        this.showList.forEach((e) => (e.clickStatus = false));
      } else {
        this.showList.forEach((e) => (e.clickStatus = true));
      }
      this.viewList = this.showList;
    },
  },
  
};
</script>
<style scoped>
@import "../assets/index.css";
</style>